<template>
  <div id="navbar">
  <b-navbar toggleable="lg" type="dark" class="white"> <!--variant="transparent"-->
    <b-navbar-brand href="#intro"><img src="../assets/logo-white.png" class="white high" alt="CODEbirds" id="logo"></b-navbar-brand>

    <b-navbar-toggle class="white" id="toggler" target="nav-collapse"></b-navbar-toggle>

    <b-collapse id="nav-collapse" is-nav>
      <!-- Right aligned nav items -->
      <b-navbar-nav class="ml-auto">

        <b-nav-item href="#about" class="white" right id="navabout" style="text-align: left;">ABOUT</b-nav-item>
        <b-nav-item href="#projects" class="white" right id="navprojects" style="text-align: left;">PROJECTS</b-nav-item>
        <b-nav-item href="#contact" class="white" right id="navcontact" style="text-align: left;">CONTACT</b-nav-item>

      </b-navbar-nav>
    </b-collapse>
  </b-navbar>

</div>
</template>

<script>
import $ from 'jquery'
window.$ = $
import 'jquery-ui/ui/effect'

export default {
  name: 'NavBar',
  props: {
    msg: String
  },
  methods: {
        handleScroll: function (event) {
          const scrollPos = $(window).scrollTop();
          if (scrollPos > 10) {
            $('#logo').switchClass('white', 'invert', 100);
            $('#logo').switchClass('high', 'low');
            $('#navabout').switchClass('white', 'invert', 100);
            $('#navprojects').switchClass('white', 'invert', 100);
            $('#navcontact').switchClass('white', 'invert', 100);
            $('#toggler').switchClass('white', 'invert', 100);
            $('#navbar').switchClass('weg', 'da');
          } else {
            $('#logo').switchClass('invert', 'white', 100);
            $('#logo').switchClass('low', 'high');
            $('#navabout').switchClass('invert', 'white', 100);
            $('#navprojects').switchClass('invert', 'white', 100);
            $('#navcontact').switchClass('invert', 'white', 100);
            $('#toggler').switchClass('invert', 'white', 100);
            $('#navbar').switchClass('da', 'weg');
          }
        }
    },

    created: function () {
        window.addEventListener('scroll', this.handleScroll);
    },
    destroyed: function () {
        window.removeEventListener('scroll', this.handleScroll);
    }
}
</script>

